@import url(https://fonts.googleapis.com/css?family=Libre+Franklin);
* {
    font-family: 'Libre Franklin', sans-serif;
    transition: 0.2s;
    transition-timing-function: ease-in;
}

html,
body {
    margin: 0;
    padding: 0;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
}

button {
    border: 1px solid rgba(0, 0, 0, 0.17);
    border-radius: 3px;
    background: none;
    padding: 1rem;
    transition: 0.05s;
}
button:hover {
    border-color: rgba(0, 0, 0, 0.33);
    cursor: pointer;
    transition: 0.05s;
}

.App {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-flow: column;
            flex-flow: column;
}

.Card {
    display: -webkit-flex;
    display: flex;
    position: absolute;
    padding: 1rem;
    border: 1px solid rgba(0, 0, 0, 0.25);
    border-radius: 1rem;
    width: 11rem;
    height: 15rem;
    box-shadow: 0px 0px 10px 3px rgba(50, 50, 50, 0.17);
    background-color: white;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-flow: column;
            flex-flow: column;
}
.Card:nth-child(2) {
    -webkit-transform: scale(0.95) translate(0, 11px);
            transform: scale(0.95) translate(0, 11px);
    box-shadow: 0px 0px 10px 3px rgba(50, 50, 50, 0.12);
}
.Card:nth-child(3) {
    -webkit-transform: scale(0.9) translate(0, 22px);
            transform: scale(0.9) translate(0, 22px);
    box-shadow: 0px 0px 10px 3px rgba(50, 50, 50, 0.05);
}
.Card:nth-child(4) {
    -webkit-transform: scale(0.85) translate(0, 33px);
            transform: scale(0.85) translate(0, 33px);
    box-shadow: 0px 0px 10px 3px rgba(50, 50, 50, 0.02);
}

.Card.clickable {
    cursor: pointer;
}
.Card.clickable:hover {
    border-color: rgba(0, 0, 0, 0.5);
}

.Card.enter-active {
    transition: 0s;
    opacity: 0;
}

.Card.exit-active {
    box-shadow: none;
    -webkit-transform: rotate(30deg) translate(240px, 40px) scale(1.2);
            transform: rotate(30deg) translate(240px, 40px) scale(1.2);
    opacity: 0;
}

.ResetCard {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100%;
    -webkit-justify-content: center;
            justify-content: center;
}

.ResetCard .check {
    background-color: green;
    width: 100px;
    height: 100px;
    -webkit-align-self: center;
            align-self: center;
    -webkit-mask-image: url(/check.svg);
            mask-image: url(/check.svg);
}

